@import "../../theme.scss";

.Headportrait-wrap {
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 99;
  text-align: center;

  &::before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }

  .Modify_the_picture {
    position: relative;
    box-shadow: 0 0.2rem 2rem 0 rgba(0, 0, 0, 0.05);
    background: #fff;
    width: 680px;
    display: inline-block;
    vertical-align: middle;
    text-align: left;

    .close-icon {
      position: absolute;
      top: 10px;
      right: 15px;
      height: 24px;
      font-size: 22px;

      color: rgba(0, 0, 0, 1);
      line-height: 24px;
      cursor: pointer;
    }

    .Modify_head {
      height: 46px;
      padding-left: 20px;
      font-size: 22px;
      line-height: 46px;
      background: #f7f7f7;
    }

    .Modify_content {
      min-height: 480px;
      padding: 18px 20px;

      p {
        color: #666;
        font-size: 14px;
      }

      .Modify_openimg {
        position: relative;
        margin-bottom: 15px;

        input {
          color: #666;
          margin: 0;
          outline: 0;
          padding: 0;
        }

        .pass-portrait-filebtn {
          border: 0;
          background: url("https://passport.baidu.com/passApi/img/setportrait_btn.png") 0 0;
          background-repeat: no-repeat;
          height: 38px;
          line-height: 38px;
          text-align: center;
          width: 130px;
        }

        .pass-portrait-file {
          height: 38px;
          opacity: 0;
          font-size: 30px;
          position: absolute;
          left: 0;
          top: 0;
          width: 130px;
        }

        .pass-portrait-msg {
          color: #999;
          display: inline-block;
          margin-left: 6px;
          text-align: left;
        }
      }

      .preview_picture {
        overflow: hidden;

        .preview_left {
          // height: 300px;
          // width: 400px;
          float: left;

          .show-img {
            height: 300px;
            width: 400px;
          }

          .preview_btn {
            display: flex;
            justify-content: space-around;
            margin-top: 15px;

            span {
              padding: 5px 20px;
              color: #fff;
              cursor: pointer;
              background-color: $bg-color;
              border-radius: 3px;
            }
          }
        }

        .preview_right {
          float: right;
          width: 145px;
          height: 300px;
          // background-color: red;
          padding-left: 20px;
          border-left: 1px solid #333;

          .before {
            width: 100px;
            height: 100px;
            overflow: hidden;
          }
        }
      }
    }

    @media screen and (max-width:680px) {
      width: 100%;

      .Modify_head {
        padding-left: 10px;
      }

      .Modify_content {
        padding: 10px;
      }
    }

    @media screen and (max-width:575px) {
      .Modify_content {
        .preview_picture {
          .preview_right {
            display: none;
          }
          .preview_left{
            float: none;
            .show-img{
              width: 100%;
            }
          }
        }
      }
    }
  }
}